
"use client";

import { useState, useEffect, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { ScrollArea } from '@/components/ui/scroll-area';
import { messageManager, Message, users } from '@/lib/constants';
import { cn } from '@/lib/utils';
import { Send, MessageSquare } from 'lucide-react';
import { formatDistanceToNow, parseISO } from 'date-fns';

type Conversation = {
    participant: string;
    messages: Message[];
    lastMessage: Message;
    unreadCount: number;
};

export default function MessagesPage() {
    const [conversations, setConversations] = useState<Conversation[]>([]);
    const [selectedConversation, setSelectedConversation] = useState<Conversation | null>(null);
    const [newMessage, setNewMessage] = useState('');
    const scrollAreaViewportRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const handleUpdate = () => {
            const convos = messageManager.getConversations('admin');
            setConversations(convos);
            
            if (selectedConversation) {
                const updatedSelected = convos.find(c => c.participant === selectedConversation.participant) || null;
                setSelectedConversation(updatedSelected);
            }
        };
        handleUpdate(); // Initial load
        const unsubscribe = messageManager.subscribe(handleUpdate);
        return () => unsubscribe();
    }, []);
    
    useEffect(() => {
        if (scrollAreaViewportRef.current) {
            scrollAreaViewportRef.current.scrollTo({
                top: scrollAreaViewportRef.current.scrollHeight,
                behavior: 'smooth',
            });
        }
    }, [selectedConversation?.messages]);

    const handleSelectConversation = (conversation: Conversation) => {
        setSelectedConversation(conversation);
        if (conversation.unreadCount > 0) {
            messageManager.markAsRead(conversation.participant, 'admin');
        }
    };

    const handleSendMessage = (e: React.FormEvent) => {
        e.preventDefault();
        if (!newMessage.trim() || !selectedConversation) return;

        const recipientUser = users.find(u => u.name === selectedConversation.participant);
        if (!recipientUser) return;

        messageManager.sendMessage({
            from: 'Olivia Martinez', // Admin's name
            to: recipientUser.role,
            content: newMessage,
        });
        setNewMessage('');
    };
    
    const formatTimestamp = (timestamp: string) => {
        try {
            return formatDistanceToNow(parseISO(timestamp), { addSuffix: true });
        } catch (error) {
            return 'Invalid date';
        }
    }

    return (
        <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-4 h-[calc(100vh-8rem)]">
            <Card className="col-span-1 md:col-span-1 lg:col-span-1 flex flex-col">
                <CardHeader>
                    <CardTitle>Conversations</CardTitle>
                </CardHeader>
                <CardContent className="p-2 flex-1">
                    <ScrollArea className="h-[calc(100vh-14rem)]">
                        <div className="space-y-1">
                            {conversations.map((convo) => (
                                <button
                                    key={convo.participant}
                                    onClick={() => handleSelectConversation(convo)}
                                    className={cn(
                                        'w-full text-left p-3 rounded-lg flex items-start gap-3 transition-colors',
                                        selectedConversation?.participant === convo.participant ? 'bg-muted' : 'hover:bg-muted/50'
                                    )}
                                >
                                    <Avatar>
                                        <AvatarImage src={`https://placehold.co/40x40.png?text=${convo.participant.charAt(0)}`} data-ai-hint="person" />
                                        <AvatarFallback>{convo.participant.charAt(0)}</AvatarFallback>
                                    </Avatar>
                                    <div className="flex-1 truncate">
                                        <div className="flex justify-between items-center">
                                            <p className="font-semibold">{convo.participant}</p>
                                             <p className="text-xs text-muted-foreground">{formatTimestamp(convo.lastMessage.timestamp)}</p>
                                        </div>
                                        <div className="flex justify-between items-start mt-1">
                                            <p className="text-sm text-muted-foreground truncate flex-1 pr-2">{convo.lastMessage.content}</p>
                                            {convo.unreadCount > 0 && (
                                                <span className="bg-primary text-primary-foreground text-xs font-bold rounded-full h-5 w-5 flex items-center justify-center flex-shrink-0">
                                                    {convo.unreadCount}
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                </button>
                            ))}
                        </div>
                    </ScrollArea>
                </CardContent>
            </Card>

            <Card className="col-span-1 md:col-span-2 lg:col-span-3 flex flex-col">
                {selectedConversation ? (
                    <>
                        <CardHeader className="border-b">
                            <CardTitle>{selectedConversation.participant}</CardTitle>
                            <CardDescription>
                                {users.find(u => u.name === selectedConversation.participant)?.role}
                            </CardDescription>
                        </CardHeader>
                        <CardContent className="flex-1 p-0">
                            <ScrollArea className="h-[calc(100vh-20rem)] p-6" viewportRef={scrollAreaViewportRef}>
                                <div className="space-y-6">
                                    {selectedConversation.messages.map((msg) => (
                                        <div key={msg.id} className={cn("flex items-end gap-3", msg.from === 'Olivia Martinez' ? "justify-end" : "justify-start")}>
                                            {msg.from !== 'Olivia Martinez' && (
                                                <Avatar className="w-8 h-8 self-start">
                                                    <AvatarImage src={`https://placehold.co/32x32.png?text=${msg.from.charAt(0)}`} data-ai-hint="person" />
                                                    <AvatarFallback>{msg.from.charAt(0)}</AvatarFallback>
                                                </Avatar>
                                            )}
                                            <div className={cn(
                                                "p-3 rounded-lg max-w-xs lg:max-w-md shadow-sm relative",
                                                msg.from === 'Olivia Martinez' 
                                                    ? "bg-primary text-primary-foreground rounded-br-none" 
                                                    : "bg-muted rounded-bl-none"
                                            )}>
                                                <p className="text-sm">{msg.content}</p>
                                                 <p className="text-xs opacity-75 mt-2 text-right">{formatTimestamp(msg.timestamp)}</p>
                                            </div>
                                             {msg.from === 'Olivia Martinez' && (
                                                <Avatar className="w-8 h-8 self-start">
                                                    <AvatarImage src={`https://placehold.co/32x32.png?text=O`} data-ai-hint="person" />
                                                    <AvatarFallback>O</AvatarFallback>
                                                </Avatar>
                                            )}
                                        </div>
                                    ))}
                                </div>
                            </ScrollArea>
                        </CardContent>
                        <div className="p-4 border-t bg-background">
                            <form onSubmit={handleSendMessage} className="flex items-center gap-2">
                                <Input
                                    value={newMessage}
                                    onChange={(e) => setNewMessage(e.target.value)}
                                    placeholder="Type a message..."
                                    className="flex-1"
                                />
                                <Button type="submit" size="icon" disabled={!newMessage.trim()}>
                                    <Send className="w-4 h-4"/>
                                    <span className="sr-only">Send</span>
                                </Button>
                            </form>
                        </div>
                    </>
                ) : (
                    <div className="flex flex-col items-center justify-center h-full text-muted-foreground p-8 text-center">
                        <MessageSquare className="w-16 h-16 mb-4" />
                        <h3 className="text-xl font-semibold">Welcome to your inbox!</h3>
                        <p>Select a conversation from the list to start chatting.</p>
                    </div>
                )}
            </Card>
        </div>
    );
}
